"use client";
import { createSupabaseClient } from "@/lib/supaBaseClient";
import { SupabaseClient } from "@supabase/supabase-js";
import { Home, PresentationIcon, TicketCheck } from "lucide-react";
import { useEffect, useState } from "react";

interface Ppts {
  name: string;
  time: string;
}

interface subs {
  req: number;
  total: number;
}

export default function page({ id }: { id: string }) {
  const [expand, setExpand] = useState<boolean>(false);
  const [ppts, setPpts] = useState<Ppts[]>([{ name: "jatt", time: "" }]);
  const [subs, setSubs] = useState<subs>();

  useEffect(() => {
    const client: SupabaseClient = createSupabaseClient();
    async function func() {
      const { data, error } = await client
        .from("company")
        .select("name,time")
        .eq("name", id);

      let arr: Array<Ppts> = [];
      data?.map((item) => {
        arr.push(item);
      });
      setPpts(arr);
    }

    async function fetchSubs() {
      const { data, error } = await client
        .from("subs")
        .select("req,total")
        .eq("name", id)
        .single();

      if (data) setSubs(data);
    }
    // fetchSubs();
    // func();
  }, []);

  function upgrade() {}

  return (
    <div className="flex h-screen w-screen bg-gradient-to-t from-slate-500 to-stone-500">
      <h1 className="barlow text-center text-4xl text-white pb-5">Dashboard</h1>
      <div className="h-full grid grid-cols-2 md:grid-cols-3 gap-5">
        {ppts?.map((item) => (
          <div className="h-[50vh] w-full flex flex-col items-center justify-center glass-effect">
            <h1 className="barlow text-white">{item.name} ppt</h1>
            <h1 className="montserrat">{item.time}</h1>
          </div>
        ))}
      </div>
      <div className="glass-effect">
        {subs?.req}/{subs?.total}
      </div>
      <button
        className={`px-6 py-3 ${"bg-gradient-to-r from-pink-500 to-purple-600"} rounded-lg text-white font-semibold shadow-md hover:from-pink-600 hover:to-purple-700 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:ring-opacity-50 transition-all duration-300`}
        onClick={upgrade}
      >
        Upgrade Subscription
      </button>
    </div>
  );
}
